import React, { useContext } from "react";
import "./NavSidebar.css";
import { Link } from "react-router-dom";
import logoImage from "../../images/logo.png";
import logoutLogo from "../../images/logout-btn.svg";
import ThemeToggler from "../ThemeTogglerNavbar/ThemeToggler";
import VideoTutorial from "../VideoTutorialNavbar/VideoTutorial";
import { ToggleSideNavbar } from "../../Contexts/ToggleSideNavbarContext";

function NavSidebar() {
	const { isSidebarOpen, toggleSideNavbar } = useContext(ToggleSideNavbar);
	return (
		<nav
			id="side-navbar-container"
			className={isSidebarOpen ? "visible-sidebar" : "hidden-sidebar"}
		>
			<div className="side-navbar-body bg-white">
				<header className="side-navbar-head border-bottom d-flex justify-content-center align-items-center flex-column">
					<img src={logoImage} className="side-navbar-head-logo" />
					<button
						type="button"
						className="close side-navbar-close-btn"
						onClick={toggleSideNavbar}
					>
						<span className="font-weight-light modal-cross-close-btn">
							&times;
						</span>
					</button>
					<h1 className="side-navbar-head-text mt-2">
						<span>TODO</span> <span>Money</span>
					</h1>
				</header>
				<div className="side-navbar-main-content">
					<ThemeToggler />
					<VideoTutorial />
				</div>
				<footer className="side-navbar-footer d-flex justify-content-between align-items-center">
					<h2 className="side-navbar-footer-username ml-3 mb-0">vaibhav</h2>
					<Link
						to="/todo-money"
						onClick={toggleSideNavbar}
						className="side-navbar-footer-logout-btn text-white d-flex justify-content-center align-items-center"
					>
						Logout <img src={logoutLogo} className="logout-image ml-2" />
					</Link>
				</footer>
			</div>
		</nav>
	);
}

export default NavSidebar;



